import React from "react";
import { Box, Wrap, WrapItem, List, ListItem, Flex, Spacer, Text, Link} from "@chakra-ui/react";
import { Heading } from "@chakra-ui/react";

const Footer = () => {
  return(
    <Box 
      className="footer" 
      bgColor="white" 
      position="relative" 
      bottom="-80px" 
      borderTop="1px solid rgba(0,0,0,0.15)" 
      h="80px"
      padding="10px 20px"
    >
      <Heading className="footer__article">
         
      </Heading>
      <Flex className="footer__content" flexDirection="column">
        <Flex justifyContent="center" gap="10px" textAlign="center" margin="10px 0px 0px 0px">
          <Link> </Link>
          <Link></Link>
          <Link></Link>
          <Link> </Link>
        </Flex>
          
        <Flex justifyContent="space-between" margin="10px 0px 10px 0px">
          <Text> 2023  Food in Flight</Text>
          <Text>   </Text>
        </Flex>
          
      </Flex>
      
    </Box>
  )
}

export default Footer